* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(240, 232, 232);
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list__item {
  display: flex;
  align-items: center;
  list-style: none;
}
.country-list__img {
  margin-right: 20px;
}
.country-list__text {
  margin: 0;
  font-size: 50px;
  font-weight: bold;
}

.country-info__item {
  list-style: none;
}
.country-info__desc {
  font-size: 20px;
  font-weight: bold;
}
.country-info__text {
  font-size: 20px;
}
